.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url("https://lh3.googleusercontent.com/-y3Kmf9AbznQ/YROPnYwVUmI/AAAAAAAAFBo/LY_updhSmHgrlNaEnA66RETK_gs0eJ0dACLcBGAsYHQ/s1600/1628671897298154-0.png");
  background-size:cover;                            background-repeat: no-repeat;                     background-size: 100% 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
